//
/*
caseServiceresource
 */
class caseService {
    constructor($q, $timeout, $http, $resource) {
        this.$q = $q;
        this.$timeout = $timeout;
        this.$http = $http;
        this.$resource = $resource;


    }
     //tree
    requestTreeRoot() {
        /*
           this.$resource('/tree/root')
         */
        return this.$resource('nodeTable.json');
    }
    //idtree
    getTreeNode(id){
        var deferred=this.$q.defer();
        var url;
        url="nodeChildren.json"
        /*
             url='/tree/'+id;
         */
        this.$http.get(url).success(
            function (data) {
                deferred.resolve(data);
            }
        ).error(function (data) {
            deferred.reject(data);
        });
        return deferred.promise;
    }
   //tree
    addTreeNode(node){
        var deferred=this.$q.defer();
        var url='/tree';
        this.$http.post(url,node).success(
            function (data) {
                deferred.resolve(data);
            }
        ).error(function (data) {
            deferred.reject(data);
        });
        return deferred.promise;
    }
    //
    requestCase() {
        return this.$resource('cases.json', {caseId: '@id'}, {});
    }
}

export default caseService;


